function addZero(num) {
    return Number(num) < 10 ? `0${num}` : num;
}

export const formatDate = (year, month, day, hour, minute, type) => {
    const newmonth = addZero(month);
    const newday = addZero(day);
    const newhour = addZero(hour);
    const newminute = addZero(minute);
    if(type === 'Time'){
        return year + '-' + newmonth + '-' + newday + ' ' + newhour + ":" + newminute;
    }else if(type === 'YearMonthDay'){
        return year + '-' + newmonth + '-' + newday;
    }else if(type === 'YearMonth'){
        return year + '-' + newmonth;
    }else if(type === 'Year'){
        return year + '';
    }
};

// 获取当前时间
export const getDate = (value, type) => {
    let date = '';
    if (value) {
        date = new Date(value);
    } else {
        date = new Date();
    }
    const y = date.getFullYear(),
        m = date.getMonth() + 1,
        d = date.getDate(),
        h = date.getHours(), //获取当前小时数(0-23)
        f = date.getMinutes();
    return formatDate(y, m, d, h, f, type);
};

// 获取对应年份月份的天数
export const getMonthDay = (year, month) => {
    var d = new Date(year, month, 0);
    return d.getDate();
};

//根据时间2019-01-02 09：12  得到 ['2019','1','2','9','12']
export const getArrWithTime = (str, type) => {
    let arr1 = str.split(' ');
    let arr2 = (arr1[0]).split('-');
    let arr3 = []
    if(type === 'Time'){
        arr3 = arr1[1].split(':');
    }
    let arr = arr2.concat(arr3);
    if(type === 'Time' || type === 'YearMonthDay' || type === 'YearMonth'){
        arr[1] = arr[1].startsWith('0') ? arr[1].substr(1, arr[1].length) : arr[1];
    }
    if(type === 'Time' || type === 'YearMonthDay' ){
        arr[2] = arr[2].startsWith('0') ? arr[2].substr(1, arr[2].length) : arr[2];
    }
    if(type === 'Time'){
        arr[3] = arr[3].startsWith('0') ? arr[3].substr(1, arr[3].length) : arr[3];
        arr[4] = arr[4].startsWith('0') ? arr[4].substr(1, arr[4].length) : arr[4];
    }
    return arr;
};

// 获取月份天数
export const getDayList = (year, month) => {
    const dayList = [];
    var d = new Date(year, month, 0);
    for (let i = 1; i <= d.getDate(); i++) {
        dayList.push(i + '');
    }

    return dayList;
};

